define(['core/ajax'], function(Ajax) {
    let isPluginEnabled = true;

    /**
     * Enables or disables the plugin
     * @param {boolean} state new state
     */
    function setState(state) {
        isPluginEnabled = state;
    }

    /**
     * Checks whether the plugin is enabled
     */
    function isEnabled() {
        return isPluginEnabled;
    }

    /**
     * Checks if the services are available, and if not, disables the plugin
     */
    function checkAvailability() {
        const request = Ajax.call([
            {
                methodname: 'block_sage_check_availability',
                args: {},
            },
        ]);

        request[0].done(function() {
            isPluginEnabled = true;
        });
        // .fail(function() {
        //     isPluginEnabled = false;
        // });
        isPluginEnabled = true;
    }

    return {
        isEnabled: isEnabled,

        setState: setState,

        checkAvailability: checkAvailability,
    };
});
